import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import {MatRippleModule} from '@angular/material/core';
// import { faInstagram } from '@fortawesome/free-brands-svg-icons';
import { faLinkedin } from '@fortawesome/free-brands-svg-icons';
import { faInstagram } from '@fortawesome/free-brands-svg-icons';
import { faEnvelope } from '@fortawesome/free-solid-svg-icons';
import { ThemeService } from '../theme.service';
import {MatSlideToggle} from '@angular/material/slide-toggle';
import {MatSlideToggleModule} from '@angular/material/slide-toggle';


@Component({
  selector: 'app-homepage',
  templateUrl: './homepage.component.html',
  styleUrls: ['./homepage.component.css']
})
export class HomepageComponent implements OnInit {
mode : boolean;
faLinkedin = faLinkedin;
faInstagram = faInstagram;
faEnvelope = faEnvelope;
// matslidetoggle : MatSlideToggle;

constructor(private x : Router, private ts: ThemeService) {}

  ngOnInit(): void {
    if(this.ts.modeBool === true) {
      this.mode = true;
    } else { this.mode = false}
    console.log(this.ts.modeBool)
    console.log(this.mode)
  }

  switchMode() {
    // this.mode = !this.mode;
    this.ts.switchMode();
    this.mode = this.ts.modeBool;
    console.log(this.mode)
    }

  goCV(){
    this.x.navigate(['cv'])
  }

  goWork(){
    this.x.navigate(['work'])
  }

  goTv(){
    this.x.navigate(['torvergata'])
  }

  goColors(){
    this.x.navigate(['colors'])
  }
}
